import { z } from "zod"
import { bigdataSchemas } from "@rp/common/contracts"
import { generateId } from "./generateId"
import { userOwner } from "./users"

function generateApiKey(
  description: string,
): z.infer<typeof bigdataSchemas.st_api_keys_CreatedApiKey> {
  const key_value = generateId()
  return {
    id: generateId(),
    stub: `${key_value.slice(0, 4)}...${key_value.slice(-4)}`,
    user_id: userOwner.ownerUserId,
    description,
    enabled: true,
    date_created: new Date().toISOString(),
    last_updated: new Date().toISOString(),
    created_by: userOwner.ownerUserId,
    modified_by: userOwner.ownerUserId,
    key_value,
  }
}

export { generateApiKey }
